.App-header {
  background: linear-gradient(
    90deg,
    rgba(30, 201, 197, 1) 0%,
    rgba(250, 255, 138, 1) 100%
  );
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.ant-table table {
  color: chocolate;
  background: rgba(255, 255, 255, 0.1) !important;
}

.ant-table-thead > tr > th {
  background: rgb(39, 94, 69) !important;
  color: #fff !important;
}
.ant-table-tbody > tr > td {
  background: rgb(90, 172, 135) !important;
  color: #fff !important;
}

h1 {
  text-shadow: 2px 2px 3px rgb(16, 46, 33);
}

.ant-pagination-item-active a {
  color: rgb(82, 61, 47) !important;
}

.tb {
  padding: 5px;
  background: rgba(255, 255, 255, 0.4) !important;
}


@media screen and (max-width: 680px) {
  img {
    display: none;
  }
}
